
.scaling-svg-container {
  position: relative;
  height: 0;
  width: 100%;
  padding: 0;
  padding-bottom: 100%;
}
.scaling-svg {
  max-height: 85vh;
  margin-top: -16px;
}
.itemLabel, .legendLabel, .areaLabel {
  font-family: 'Oswald', sans-serif;
  opacity: 1;
  -webkit-transform-origin: top right;
          transform-origin: top right;
  -moz-transform-origin: 0 0;
}
.itemLabel:hover {
  font-weight: bold;
}
.itemLabel {
  cursor: pointer;
  fill: #333;
}
.areaFill {
  opacity: 0.5;
}
.areaLabel {
  fill: rgba(255, 255, 255, 0.7);
}
.areaLabelArc {
  fill: none;
}
.legendLabel {
  fill: #333;
}
.legendArcBg {
  /*fill: red;*/
  fill: transparent;
}
.legendArcOuter {
  fill: rgba(255, 255, 255, 0.5);
}





